<template>
<main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'main'}">
            
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'cart'}">
            
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
             
          </a>
        </li>
      </ul>

      <h1 class="content__title">
          <span v-if="orderInfoData"> {{orderInfoData.id}}</span>
      </h1>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <p class="cart__message">
             &nbsp;  . &nbsp;    &nbsp; . 
               &nbsp; &nbsp;     .
          </p>

          <ul class="dictionary">
            <li class="dictionary__item">
              <span class="dictionary__key">
                
              </span>
              <span class="dictionary__value" v-if="orderInfoData">
                {{ orderInfoData.name }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                 
              </span>
              <span class="dictionary__value" v-if="orderInfoData">
                {{ orderInfoData.address }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                
              </span>
              <span class="dictionary__value" v-if="orderInfoData">
                {{ orderInfoData.phone }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Email
              </span>
              <span class="dictionary__value" v-if="orderInfoData">
                {{ orderInfoData.email }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                 
              </span>
              <span class="dictionary__value" v-if="orderInfoData">
                <b>{{ orderInfoData.paymentType }}</b>
              </span>
            </li>
          </ul>
        </div>

        <div class="cart__block" v-if="orderInfoData && orderInfoData.basket && orderInfoData.basket.items">
          <ul class="cart__orders" v-for="item in orderInfoData.basket.items" :key="item.id">
            <li class="cart__order">
              <h3>{{item.product.title}}</h3>              
              <b>{{item.product.price}} </b>  
              <span>SKU: {{ generateSku(item.product.id, item.color.color.id, item.size.id) }}</span>
              <span>: {{ item.color.color.title}}, -: {{item.quantity }} , : {{ item.size.title }}</span>
            </li>            
          </ul>
          
          <div class="cart__total">
            <p>: 
              <b>{{ orderInfoData.deliveryType.title }}</b><br>
              <b>{{ orderInfoData.deliveryType.price }} </b></p>
            <p>: <b>{{orderInfoData.basket.items.length }}</b>    <b>{{orderInfoData.totalPrice}} </b></p>
          </div>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import { mapState } from 'vuex';
import  useProduct from '@/hooks/useProduct';

export default{
  setup() {
    const { generateSku } = useProduct();
    return {
      generateSku
    };
  },
computed: {
  ...mapState({
    data: state => state.orderInfo
  }),
  orderInfoData(){
    return this.$store.state.orderInfo;
  }
},
created(){
if(this.$store.state.orderInfo && this.$store.state.orderInfo.id === this.$route.params.id) {
  return;
}

  this.$store.dispatch('loadOrderInfo', this.$route.params.id);
}
}
</script>